import React from 'react';

import '../App.css';

const AboutMe = () => (
  <div className="about-me code-block dark-theme">
    <div className="tag">{'<AboutMe>'}</div>
    <div className="indented-text">
      I am motivated to be a software developer because I enjoy problem solving, working in a trusting and
      collaborative environment, and designing and implementing elegant designs. I began my professional career as a
      change management consultant but in 2018 I decided I wanted a different career path. So, I applied to
      Makers Academy and learned to code in just 4 months.
    </div>
    <div className="tag">{'</AboutMe>'}</div>
  </div>
);

export default AboutMe;
